import React, { useState, useEffect } from "react";
import { useToasts } from "react-toast-notifications";
// import classNames from "classnames";

const Calc = () => {
  const { addToast } = useToasts();
  const inputControls = [
    { key: "key-ac", value: "AC", displayValue: "AC", isActionInput: false },
    {
      key: "key-plus-minus",
      value: "+/-",
      displayValue: "±",
      isActionInput: true,
    },
    { key: "key-percent", value: "%", displayValue: "%", isActionInput: true },
    { key: "key-7", value: "7", displayValue: "7", isActionInput: false },
    { key: "key-8", value: "8", displayValue: "8", isActionInput: false },
    { key: "key-9", value: "9", displayValue: "9", isActionInput: false },
    { key: "key-4", value: "4", displayValue: "4", isActionInput: false },
    { key: "key-5", value: "5", displayValue: "5", isActionInput: false },
    { key: "key-6", value: "6", displayValue: "6", isActionInput: false },
    { key: "key-1", value: "1", displayValue: "1", isActionInput: false },
    { key: "key-2", value: "2", displayValue: "2", isActionInput: false },
    { key: "key-3", value: "3", displayValue: "3", isActionInput: false },
    { key: "key-0", value: "0", displayValue: "0", isActionInput: false },
    { key: "key-dot", value: ".", displayValue: ".", isActionInput: false },
    {
      key: "key-erase",
      value: "erase",
      displayValue: "\u232b",
      isActionInput: true,
    },
  ];
  const actionControls = [
    { key: "divide", value: "/", displayValue: "÷", isActionInput: true },
    { key: "multiply", value: "*", displayValue: "×", isActionInput: true },
    { key: "minus", value: "-", displayValue: "-", isActionInput: true },
    { key: "plus", value: "+", displayValue: "+", isActionInput: true },
    { key: "equal", value: "=", displayValue: "=", isActionInput: true },
  ];

  const [inputVal, setInputVal] = useState(0);
  const [currentInput, setCurrentInput] = useState(0);

  const handleInputClickEvent = (e, value) => {
    e.preventDefault();
    const valueDownCase = value.toLowerCase();

    switch (valueDownCase) {
      case "erase":
        eraseValue();
        break;
      case "ac":
        setInputVal(0);
        break;
      case "%":
        calcPercentage();
        break;
      case "+/-":
        calcPlusMinus();
        break;
      case "/":
      case "*":
      case "-":
      case "+":
        setInputValue(value, 1);
        break;
      case "=":
        calcResult();
        break;
      default:
        setInputValue(value, 0);
    }
  };

  const eraseValue = () => {
    let inputValue = inputVal.toString();
    if (inputValue && inputValue !== "" && inputValue !== "0") {
      inputValue = inputValue.slice(0, -1);
      setInputVal(inputValue);
    }
  };

  const validateInputStr = (inputStr) => {
    let inputValue = inputStr.toString();
    let lastChar = inputValue.slice(inputValue.length - 1);

    if ("+-*/".includes(lastChar) && "+-*/".includes(currentInput)) {
      inputValue = inputValue.slice(0, -1);
    }
    return inputValue;
  };

  const setInputValue = (value, isActionTrigger) => {
    console.log(value);
    if (value) {
      let inputValue = inputVal.toString();
      if (inputValue.slice(0, 1) === "0") {
        inputValue = inputValue.slice(1);
      }
      if (inputValue.includes(".") && value.includes(".")) {
        return false;
      }
      inputValue += value;
      if (inputValue === "." || inputValue < 1 || inputValue.slice(0, 1) === ".") {
        inputValue = "0" + inputValue;
      }
      if (isActionTrigger) {
        inputValue = validateInputStr(inputValue);
        if (inputValue === "0" || inputVal === 0) {
          return false;
        }
      }
      // if (inputValue !== "") {
      setCurrentInput(value);
      setInputVal(inputValue);
      // }
    }
  };

  const calcPercentage = () => {
    let inputValue = inputVal;
    if (inputValue !== 0) {
      inputValue = validateInputStr(inputValue);
      const result = inputValue / 100;
      setInputVal(result);
    }
  };

  const calcPlusMinus = () => {
    let inputValue = inputVal;
    inputValue = validateInputStr(inputValue);
    try {
      let result = eval(inputValue);
      if (result) {
        if (result < 0) {
          result = Math.abs(result);
        } else {
          result = result * -1;
        }
        setInputVal(`${result}`);
      }
    } catch (err) {
      console.error(err);
      addToast("Invalid input. Please check the input value.", {
        appearance: "error",
        autoDismiss: true,
      });
    }
  };

  const calcResult = () => {
    if (inputVal && inputVal !== "") {
      const inputValue = validateInputStr(inputVal);
      try {
        const result = eval(inputValue);
        if (result) {
          setInputVal(`${result}`);
        }
      } catch (err) {
        console.error(err);
        addToast("Invalid input. Please check the input value.", {
          appearance: "error",
          autoDismiss: true,
        });
      }
    }
  };

  const handleKeyboardEvent = (event) => {
    const { code, key, keyCode } = event;

    if (key === "d" || key === "D") {
      document.body.classList.toggle("mode-dark");
    }
    if (code.includes("Digit") || code.includes("Numpad")) {
      if (
        (keyCode >= 48 && keyCode <= 57) ||
        (keyCode >= 96 && keyCode <= 105)
      ) {
        setInputValue(key.toString());
      }
    }
  };

  useEffect(() => {
    document.addEventListener("keydown", handleKeyboardEvent);
    return () => {
      document.removeEventListener("keydown", handleKeyboardEvent);
    };
  }, []);

  const inputControlsElem = inputControls.map((el, index) => (
    <li
      className={el.isActionInput ? "action-input" : "normal-input"}
      key={el.key}
      onClick={(e) => handleInputClickEvent(e, el.value)}
    >
      {el.displayValue}
    </li>
  ));
  const actionControlsElem = actionControls.map((el, index) => (
    <li
      className={el.isActionInput ? "action-input" : "normal-input"}
      key={el.key}
      onClick={(e) => handleInputClickEvent(e, el.value)}
    >
      {el.displayValue}
    </li>
  ));

  return (
    <div className={inputVal == 0 ? "app disable" : "app"}>
      <h1 className="app--title">React Calculator</h1>
      <div className="app-wrapper">
        <input
          type="text"
          className="app-wrapper--input"
          name="calcinput"
          value={inputVal}
          readOnly
        />
        <div className="app-wrapper-input-action-btn">
          <ul className="calc-btn-list input-elem">{inputControlsElem}</ul>
          <ul className="calc-btn-list action-elem">{actionControlsElem}</ul>
        </div>
      </div>
    </div>
  );
};

export default Calc;
